body, html {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

.bg {
  background-image: url(/signin-signup-form/static/media/bg-cover.280d44c1.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  color: white;
}

.appLogoBox {
  width: 60%;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0.6;
}

.appForm {
  width: 40%;
  background-color: #2E4158;
  padding: 25px 40px;
  overflow: auto;
}

@media (max-width: 540px) {
  .appLogoBox {
    display: none;
  }

  .formField__Button {
    padding: 5px 15px;
  }

  .appForm {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .appLogoBox {
    width: 30%;
  }

  .formField__Button {
    padding: 15px 15px;
  }

  .appForm {
    width: 70%;
  }
}

.pageSwitcher {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-bottom: 10%;
  background-color: #3493e0;
  color: white;
  padding: 10px 25px;
  cursor: pointer;
  font-size: .9em;
  border: none;
  outline: none;
  display: inline-block;
  text-decoration: none;
  border-radius: 25px;
}

.pageSwitcher:hover {
  background-color: #2376bb;
}

.formCenter {
  margin-bottom: 100px;
}

.formTitle {
  color: #707C8B;
  font-weight: 300;
  margin-bottom: 50px;
}

.formTitle__Link {
  color: #707C8B;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  padding-bottom: 5px;
}

.formTitle__Link:first-child {
  margin-left: 0;
}

.formTitle__Link--Active {
  color: white;
  border-bottom: 1px solid #199087;
}

.formField {
  margin-bottom: 40px;
}

.formField__SignUp {
  margin-bottom: 15px;
}
.formField__SUCB {
  margin-bottom: 10px;
  margin-top: 15px;
}

.formField__Label {
  display: block;
  text-transform: uppercase;
  font-size: .9em;
  color: white;
  margin-bottom: 10px;
}

.formField__Input {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid #445366;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
}

.formField__Input::-webkit-input-placeholder {
  color: #616E7F;
}

.formField__Input::-ms-input-placeholder {
  color: #616E7F;
}

.formField__Input::placeholder {
  color: #616E7F;
}

.formField__Button {
  background-color: #52C4B9;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 70px;
  font-size: .8em;
  font-weight: 500;
  cursor: pointer;
}

.formField__Button:hover {
  background-color: #199087
}

.formField__Link {
  color: #66707D;
  text-decoration: none;
  display: inline-block;
  border-bottom: 1.5px solid #225E62;
  padding-bottom: 5px;
}

.formField__CheckboxLabel {
  color: white;
  font-size: .9em;
}

.formField__Checkbox {
  position: relative;
  top: 1.5px;
}

.formField__TermsLink {
  color: white;
  border-bottom: 1px solid #199087;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  margin-left: 5px;
}



.formContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background: #2c3b4b;
}
 


a {
  color: #8c949c;
  font-weight: bold;
  text-decoration: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 20vh;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


